___CSS_LOADER_EXPORT___.push([module.id, `.notes-output {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.notes-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #e0e0e0;
}

.notes-header h3 {
  margin: 0;
  color: #333;
  font-size: 1.3rem;
  font-weight: 600;
}

.button-group {
  display: flex;
  gap: 12px;
}

.copy-button,
.download-button {
  padding: 8px 16px;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

.copy-button {
  background-color: #667eea;
}

.copy-button:hover {
  background-color: #5a6fd8;
}

.download-button {
  background-color: #28a745;
}

.download-button:hover {
  background-color: #218838;
}

.notes-content {
  padding: 20px;
  max-height: 400px;
  overflow-y: auto;
}

.notes-content pre {
  margin: 0;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
  font-size: 0.95rem;
  line-height: 1.5;
  color: #333;
