___CSS_LOADER_EXPORT___.push([module.id, `.transcript-input {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 24px;
}

.transcript-input-header h3 {
  color: #2563eb;
  margin: 0 0 8px 0;
  font-size: 1.2rem;
}

.transcript-input-header p {
  color: #6b7280;
  margin: 0 0 20px 0;
  font-size: 0.9rem;
}

.transcript-input-methods {
  margin-bottom: 20px;
}

.file-upload-section {
  margin-bottom: 16px;
}

.file-upload-button {
  display: inline-block;
  background: #f3f4f6;
  border: 2px dashed #d1d5db;
  border-radius: 8px;
  padding: 12px 16px;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.9rem;
  color: #4b5563;
}

.file-upload-button:hover {
  background: #e5e7eb;
  border-color: #9ca3af;
}

.file-upload-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.transcript-textarea {
  width: 100%;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 12px;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  resize: vertical;
  min-height: 200px;
}

.transcript-textarea:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.transcript-input-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 16px;
}

.process-button {
  background: #2563eb;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s;
}

.process-button:hover:not(:disabled) {
  background: #1d4ed8;
}

.process-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.clear-button {
  background: #f3f4f6;
  color: #6b7280;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 12px 16px;
  cursor: pointer;
  transition: all 0.2s;
}

.clear-button:hover:not(:disabled) {
  background: #e5e7eb;
}

.transcript-preview {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 12px;
}

.preview-label {
  font-size: 0.8rem;
  color: #6b7280;
  margin: 0 0 8px 0;
}

.preview-text {
  font-family: 'Courier New', monospace;
  font-size: 0.8rem;
  color: #4b5563;
  line-height: 1.4;
